.footer{
    width: 100%;
    padding: 3rem 0;
    background-color: navy;
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 70px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0.2rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;

}

.location p{
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
    margin-top: 0.1rem;
    font-size: 0.7rem;
}

h4{
    font-size: 1.1rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1.2rem;
}

@media screen and (max-width: 640px) {
    .footer-container{
        grid-template-columns: repeat 1fr;
    }
}